import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';
import { LoginService } from './api/login.service';
import { User } from './system.service';
import { UtilsService } from './utils.service';

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  isLogged = false;
  redirectUrl = `${environment.systemName}/home`;

  private u: User = new User();

  constructor(private loginService: LoginService, private utils: UtilsService, private router: Router) {
    if (this.user.account && this.user.token) {
      const now = new Date().getTime();
      if (this.user.token.token && this.user.token.expireTime > now) {
        this.isLogged = true;
      }
    }
  }

  get user(): User {
    if (!this.u.account) {
      const u = this.utils.getSession('user');
      this.u = Object.assign(new User(), u);
      this.user = this.u;
    }
    return this.u;
  }

  set user(u: User) {
    this.utils.setSession('user', u);
    this.u = u;
  }

  removeSession(): void {
    this.utils.removeSession('user');
  }

  login(account: string, password: string): Promise<User> {
    this.removeSession();
    return new Promise((resolve, reject) => {
      this.loginService.login(account, password).subscribe(
        (u: User) => {
          if (u) {
            this.user = Object.assign({}, u);
            this.isLogged = true;
            resolve(u);
          }
        },
        (e) => {
          reject(e);
        },
        () => {
          resolve(new User());
        },
      );
    });
  }

  logout(): void {
    this.removeSession();
    this.user = new User();
    this.router.navigate([`/passport/login`]);
  }
}
