import { Injectable, Injector } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  CanActivateChild,
  CanLoad,
  Route,
  Router,
  RouterStateSnapshot,
  UrlSegment,
  UrlTree,
} from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { AuthService } from './auth.service';
import { Menu, MenuMap } from './system.service';

@Injectable({
  providedIn: 'root',
})
export class AuthGuard implements CanActivate, CanActivateChild, CanLoad {
  constructor(public auth: AuthService, public router: Router, private injector: Injector) {}

  get message(): NzMessageService {
    return this.injector.get(NzMessageService);
  }

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot,
  ): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return this.checkLogin(state.url);
  }
  canActivateChild(
    childRoute: ActivatedRouteSnapshot,
    state: RouterStateSnapshot,
  ): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return this.canActivate(childRoute, state);
  }

  checkLogin(currentUrl: string): boolean {
    if (this.auth.isLogged) {
      return true;
    }
    this.auth.redirectUrl = currentUrl;
    this.router.navigate([`/passport/login`]);
    return false;
  }

  checkLoad(route: Route): boolean {
    if (route.path === environment.systemName || this.checkMenu(`${route.path}`)) {
      return true;
    }

    this.message.error('你没有当前页面的访问权限');
    return false;
  }

  checkMenu(path: string): boolean {
    const menus: MenuMap = this.auth.user.permissions?.menus as MenuMap;

    for (const key in menus) {
      if (Object.prototype.hasOwnProperty.call(menus, key)) {
        const ms: Menu[] = menus[key];

        const result = this.findMenu(ms, path);
        if (result) {
          return true;
        }
      }
    }

    return false;
  }

  findMenu(menus: Menu[], path: string): boolean {
    let result = false;
    if (!menus) {
      return result;
    }

    for (let i = 0; i < menus.length; i++) {
      const m = menus[i];
      if (m.path === path) {
        result = true;
        break;
      }

      if (m.children) {
        const menu = this.findMenu(m.children, path);
        if (menu) {
          result = true;
          break;
        }
      }
    }
    return result;
  }

  canLoad(route: Route, segments: UrlSegment[]): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return this.checkLogin(route.path as string) && this.checkLoad(route);
  }
}
