import { Routes } from '@angular/router';
import { environment } from 'src/environments/environment';
import { AuthGuard } from 'src/service/auth.guard';

// 公共路由
export const shareRoutes: Routes = [
  // 异常模块
  {
    path: '',
    loadChildren: () => import('../pages/system/exception/exception.module').then((x) => x.ExceptionModule),
    data: {
      animation: 'exception',
      breadcrumb: '异常',
    },
  },
];

// 顶级路由，指向框架页
export const mainRoutes: Routes = [
  // 如果路由为空就指向 index
  { path: '', redirectTo: `${environment.systemName}`, pathMatch: 'full' },

  // index
  {
    path: `${environment.systemName}`,
    loadChildren: () => import('../../layout/default/default.module').then((x) => x.DefaultModule),
    canActivateChild: [AuthGuard],
    canLoad: [AuthGuard],
  },
  // index
  {
    path: `passport`,
    loadChildren: () => import('../../layout/full/full.module').then((x) => x.FullModule),
  },

  ...shareRoutes,
];

export const fullRoutes: Routes = [
  // 登录页
  {
    path: 'login',
    loadChildren: () => import('../pages/login/login.module').then((x) => x.LoginModule),
  },
  ...shareRoutes,
];

