import { mainRoutes } from './routes/routes';
import { NgModule } from '@angular/core';
import { Routes, RouterModule, RouteReuseStrategy } from '@angular/router';
import { PreloadingStrategyService } from 'src/service/preloading-strategy.service';
import { ReuseStrategyService } from 'src/service/reuse-strategy.service';

@NgModule({
  imports: [
    RouterModule.forRoot(mainRoutes, {
      enableTracing: false,
      anchorScrolling: 'enabled',
      onSameUrlNavigation: 'reload',
      preloadingStrategy: PreloadingStrategyService,
      relativeLinkResolution: 'legacy',
    }),
  ],
  exports: [RouterModule],
  providers: [
    PreloadingStrategyService,
    { provide: RouteReuseStrategy, useClass: ReuseStrategyService },
  ],
})
export class AppRoutingModule {}
