import { Injectable } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { filter, map, mergeMap } from 'rxjs/operators';
import { NavService } from './nav.service';
import { UtilsService } from './utils.service';
import * as _ from 'lodash';
import { MenuService } from './menu.service';

@Injectable({
  providedIn: 'root',
})
export class SystemService {
  private key = 'System';
  // 默认主页
  private homePage: Tab = {
    level: 1,
    title: '主页',
    icon: 'home',
    path: 'home',
    open: true,
    selected: false,
    disabled: false,
  };

  // 菜单变更事件
  private menuChange = new Subject<{
    tabPages: Tab[];
    current: string;
  }>();

  // 当前会话存储
  private currentSession!: Session;

  private menus: Menu[] = [];

  constructor(
    public utils: UtilsService,
    public router: Router,
    public nav: NavService,
    private menu: MenuService,
    public activatedRoute: ActivatedRoute,
  ) {
    this.removeSession();
    menu.change$.subscribe((menus: Menu[]) => {
      this.menus = menus;
    });
    // 路由事件
    this.router.events
      .pipe(
        filter((event) => event instanceof NavigationEnd),
        map(() => this.activatedRoute),
        map((route) => {
          while (route.firstChild) {
            route = route.firstChild;
          }
          return route;
        }),
        filter((route) => route.outlet === 'primary'),
        mergeMap((route) => route.data),
      )
      .subscribe((event) => {
        this.setTabs();
      });
  }

  /**
   * 菜单变更事件的观察对象
   */
  get menu$(): Observable<{ tabPages: Tab[]; current: string }> {
    return this.menuChange.asObservable();
  }

  /**
   * 获取session
   */
  get session(): Session {
    if (!this.currentSession) {
      this.currentSession = Object.assign(
        {
          activatedPage: 'home',
          tabPages: [this.homePage],
        },
        this.utils.getSession(this.key),
      );
      this.utils.setSession(this.key, this.currentSession);
    }
    return this.currentSession;
  }

  /**
   * 设置session值
   */
  set session(value: Session) {
    this.currentSession = Object.assign(
      this.currentSession
        ? this.currentSession
        : {
            activatedPage: 'home',
            tabPages: [this.homePage],
          },
      value,
    );
    this.utils.setSession(this.key, this.currentSession);
  }

  /**
   * 移除当前会话存储
   */
  removeSession(): void {
    this.utils.removeSession(this.key);
  }

  /**
   * 标签页处理
   */
  setTabs(): void {
    const url = this.nav.getUrl(this.router.url);
    const routers = url.path.split('/');
    if (routers.length > 2) {
      const path = routers[2];
      const subPage = routers.length > 3 ? _.drop(routers, 3).join('/') : undefined;
      const param = url.param;
      const menu = this.findMenu(this.menus, path);
      if (menu) {
        const tabPages = this.session.tabPages as Tab[];
        const tab = _.find(tabPages, (x) => x.path === menu.path);
        if (tab) {
          tab.subPage = subPage;
          tab.param = param;
        } else {
          menu.subPage = subPage;
          menu.param = param;
          tabPages.push(menu);
        }
        this.session = {
          activatedPage: path,
          subPage,
          param,
          tabPages,
        };
        this.session.tabPages = [...(this.session.tabPages as Tab[])];
        this.menuChange.next({
          tabPages: this.session.tabPages,
          current: path,
        });
      }
    }
  }

  findMenu(menus: Tab[], path: string): Tab | null {
    let menu: Tab | null = null;
    if (!menus) {
      return null;
    }

    for (let i = 0; i < menus.length; i++) {
      const m = menus[i];
      if (m.path === path) {
        menu = m;
        break;
      }

      if (m.children) {
        menu = this.findMenu(m.children, path);
        if (menu) {
          break;
        }
      }
    }
    return menu;
  }
}

/**
 * 当前会话
 *
 * @export
 */
export interface Session {
  // 当前激活的页面
  activatedPage?: string;
  // 当前激活页面的子页面
  subPage?: string;
  // 当前激活页面的参数
  param?: { [property: string]: any };
  // 标签页数据
  tabPages?: Tab[];
}

/**
 * tab数据
 */
export interface Tab extends Menu {
  // 子路由页面
  subPage?: string;
  // 参数
  param?: { [property: string]: any };
}

/**
 * 菜单数据
 */
export class Menu {
  level?: number;
  title?: string;
  icon: string;
  open?: boolean;
  path?: string;
  selected?: boolean;
  disabled?: boolean;
  children?: Menu[];
  [prototype: string]: any;
  constructor() {
    this.icon = '';
  }
}

export interface MenuMap {
  [key: string]: Menu[];
}

export class User {
  // 账户ID
  account = '';
  // 姓名
  name = '';
  // token
  token: Token = new Token();
  // 权限
  permissions: {
    actions?: Action[];
    menus?: MenuMap;
  } = { actions: [], menus: {} };
}

export class Token {
  // 认证ID
  token = '';
  // 过期时间
  expireTime = 0;
}

export interface Action {
  // 路由
  path: string;
  // 读
  read?: boolean;
  // 新增
  add?: boolean;
  // 修改
  edit?: boolean;
  // 删除
  delete?: boolean;
  // 其他
  [key: string]: any;
}
