import * as _ from 'lodash';
import {
  RouteReuseStrategy,
  ActivatedRouteSnapshot,
  DetachedRouteHandle,
} from '@angular/router';

export interface RouteReuseStorage {
  key: string;
  handle: DetachedRouteHandle;
}

/**
 * 路由复用策略
 */
export class ReuseStrategyService implements RouteReuseStrategy {
  // 存储的复用路由
  public static storages: RouteReuseStorage[] = [];
  // 用一个临时变量记录待删除的路由
  private static waitDelete: string | null;

  /**
   * 删除复用的路由
   * @param name 路由名称
   */
  public static deleteRouteSnapshot(name?: string): void {
    if (name) {
      const key = name.replace(/\//g, '_');
      _.remove(ReuseStrategyService.storages, (x) => x.key.indexOf(key) === 0);
      ReuseStrategyService.waitDelete = key;
    } else {
      ReuseStrategyService.storages = [];
    }
  }

  /**
   * 表示对所有路由允许复用 如果你有路由不想利用可以在这加一些业务逻辑判断
   *
   * @param route 路由镜像
   */
  public shouldDetach(route: ActivatedRouteSnapshot): boolean {
    return true;
  }

  /**
   * 当路由离开时会触发。按path作为key存储路由快照&组件当前实例对象
   *
   * @param route 路由镜像
   * @param handle handle
   */
  public store(
    route: ActivatedRouteSnapshot,
    handle: DetachedRouteHandle
  ): void {
    if (handle === null) {
      return;
    }
    if (
      ReuseStrategyService.waitDelete &&
      this.getRouteUrl(route).indexOf(ReuseStrategyService.waitDelete) === 0
    ) {
      // 如果待删除是当前路由则不存储快照
      ReuseStrategyService.waitDelete = null;
      return;
    }
    this.add(this.getRouteUrl(route), handle);
  }

  /**
   * 若 path 在缓存中有的都认为允许还原路由
   *
   * @param route 路由镜像
   */
  public shouldAttach(route: ActivatedRouteSnapshot): boolean {
    return !!_.find(
      ReuseStrategyService.storages,
      (x) => x.key === this.getRouteUrl(route)
    );
  }

  /**
   * 从缓存中获取快照，若无则返回nul
   *
   * @param route 路由镜像
   */
  public retrieve(route: ActivatedRouteSnapshot): DetachedRouteHandle | null {
    if (!route.routeConfig) {
      return null;
    }
    const stroage = _.find(
      ReuseStrategyService.storages,
      (x) => x.key === this.getRouteUrl(route)
    );
    return stroage ? stroage.handle : null;
  }

  /**
   * 进入路由触发，判断是否同一路由
   * 解决不同的参数也会认为是同一个路由，导致会将之前的路由拿出来复用
   *
   * @param future 前一个路由
   * @param curr 当前路由
   */
  public shouldReuseRoute(
    future: ActivatedRouteSnapshot,
    curr: ActivatedRouteSnapshot
  ): boolean {
    return (
      future.routeConfig === curr.routeConfig &&
      JSON.stringify(future.params) === JSON.stringify(curr.params)
    );
  }

  /**
   * 解决不同的主路由会存在相同名称的子路由
   * @param route 路由
   * @returns url
   */
  private getRouteUrl(route: ActivatedRouteSnapshot): string {
    const url = (route as any)._routerState.url.replace(/\//g, '_');
    // if (!route.routeConfig.loadChildren) {
    //     url += `${route.routeConfig.component.toString().split('(')[0].split(' ')[1]}`
    // }
    // + '_' + (route.routeConfig.loadChildren || route.routeConfig.component.toString().split('(')[0].split(' ')[1]);
    return url;
  }

  private add(key: string, handle: DetachedRouteHandle): void {
    _.remove(ReuseStrategyService.storages, (x) => x.key === key);
    ReuseStrategyService.storages = [
      ...ReuseStrategyService.storages,
      { key, handle },
    ];
  }
}
