import { Injectable } from '@angular/core';
import { FormGroup } from '@angular/forms';

@Injectable({
  providedIn: 'root',
})
export class UtilsService {
  constructor() {}

  /**
   * 获取本地值
   *
   * @param  key 关键字
   */
  getLocal(key: string): any {
    return JSON.parse(localStorage.getItem(key) || 'null') || null;
  }

  /**
   * 设置本地值
   *
   * @param  key 关键字
   * @param  value 值
   */
  setLocal(key: string, value: any): void {
    localStorage.setItem(key, JSON.stringify(value));
  }

  /**
   * 获取当前会话的值
   *
   * @param  key 关键字
   * @returns 值
   */
  getSession(key: string): any {
    return JSON.parse(sessionStorage.getItem(key) || 'null') || null;
  }

  /**
   * 设置当前会话值
   *
   * @param  key 关键字
   * @param  value 值
   */
  setSession(key: string, value: any): void {
    sessionStorage.setItem(key, JSON.stringify(value));
  }

  /**
   * 移除本地值
   *
   * @param  key 关键字
   */
  removeLocal(key: string): void {
    localStorage.removeItem(key);
  }

  /**
   * 移除当前会话
   *
   * @param  key 关键字
   */
  removeSession(key: string): void {
    sessionStorage.removeItem(key);
  }

  /**
   * 设置表单中只定key的值
   * @param form 表单
   * @param key 键
   * @param value 值
   */
  setFormValue(form: FormGroup, key: string, value: any): void {
    const formValue: { [prop: string]: any } = {};
    formValue[key] = value;
    form.patchValue(formValue);
  }

  /**
   * 初始化值替换
   * @param from 前
   * @param to 后
   */
  mapToObject(
    from: { [prop: string]: any },
    to: { [prop: string]: any }
  ): void {
    for (const key in from) {
      if (typeof to[key] === 'undefined') {
        to[key] = from[key];
      }
    }
  }

  /**
   * 生成guid
   */
  guid(): string {
    const S4 = () => {
      return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
    };
    return (
      S4() +
      S4() +
      '-' +
      S4() +
      '-' +
      S4() +
      '-' +
      S4() +
      '-' +
      S4() +
      S4() +
      S4()
    );
  }

  /**
   * 替换值
   * @param str 需要做替换处理的字符串
   * @param obj 替换的对象 key-value
   * @param prop 对象属性模块， 默认 '$[prop]'
   */
  replace(
    str: string,
    obj: { [prop: string]: any },
    tpl: string = '$[prop]'
  ): string {
    if (str && obj) {
      for (const key in obj) {
        const replaceStr = tpl.replace('prop', key);
        str = str.replace(replaceStr, obj[key]);
      }
      return str;
    }
    return str;
  }
}
