import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { Menu } from './system.service';

@Injectable({
  providedIn: 'root',
})
export class MenuService {
  private menus = [
    {
      level: 1,
      title: '主页',
      icon: 'home',
      path: 'home',
      open: true,
      selected: false,
      disabled: false,
    },
    {
      level: 1,
      title: '仪表盘',
      icon: 'dashboard',
      path: 'dashboard',
      open: false,
      selected: false,
      disabled: false,
    },
  ];

  private change: BehaviorSubject<Menu[]> = new BehaviorSubject<Menu[]>(this.defaultMenu);

  constructor() {}

  get defaultMenu(): Menu[] {
    return this.menus;
  }

  get change$(): Observable<Menu[]> {
    return this.change.asObservable();
  }

  setMenu(menus: Menu[]): void {
    this.change.next(menus);
  }
}
